.product {
    &:hover {
        & img {
            opacity: 0.9;
        }
    }
}

.product {
    background-color: #fff;
    border: 1px solid #ddd;
    & h2 {
        margin-bottom: 10px;
        text-align: center;
        font-size: 1.6rem;
        padding: 0 20px;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
    }

    & p {
        text-align: center;
    }
}

.product-image {
    margin-bottom: 10px;
    cursor: pointer;

    & img {
        aspect-ratio: 1 / 1 ;
    }
}

.product-detail {
    width: 950px;
    padding: 20px;

    & h2 {
        font-size: 2.2rem;
        margin-bottom: 10px;
    }
}