.categories {

    & .light {
        background-color: rgb(248,249,250);
    }

    & ul {
        padding-left: 2rem;
        margin-bottom: 10px;

        & li {
            line-height: 40px;
            height: 40px;
            overflow: hidden;
            cursor: pointer;

            & a {
                color: #333;
                display: block;

                &:hover {
                    color: rgb(195, 123, 0);
                }
            }

            
        }
    }

    & h4 {
        padding: 6px 2rem;
        font-size: 1.6rem;
        font-weight: 600;
    }

    & h5 {
        font-weight: 500;
        text-transform: uppercase;
        margin-bottom: 15px;
    }
    & ul {
        list-style: none;
    }
}