.page-title {
    background-color: #fff;
    padding: 6rem 50px;
    font-weight: 500;
    text-transform: uppercase;

    & h2 {
        margin-bottom: 0;
    }

    
}

.slug-link {
    display: flex;
    color: #999;

    & a {
        color: #333;
    }

    & > * {
        padding: 0 10px;
    }

    & span::before {
        padding-right: 1.5rem;
        color: #ced4da;
        content: '/';
    }
}